import React, {
  ChangeEvent,
  DetailedHTMLProps,
  InputHTMLAttributes,
  KeyboardEvent,
  ReactNode
} from 'react'
import s from './SuperInputText.module.css'

//    
type DefaultInputPropsType = DetailedHTMLProps<InputHTMLAttributes<HTMLInputElement>, HTMLInputElement>

//               
// (   value: string, onChange: ...;      DefaultInputPropsType)
type SuperInputTextPropsType = DefaultInputPropsType & { //  +       
    onChangeText?: (value: string) => void
    onEnter?: () => void
    error?: ReactNode
    spanClassName?: string
    /*  onChange */
    // onChange?: (e: ChangeEvent<HTMLInputElement>)=> void
}

const SuperInputText: React.FC<SuperInputTextPropsType> = (
    {
        type, //          
        onChange, onChangeText,
        onKeyPress, onEnter,
        error,
        className, spanClassName,

        ...restProps//       restProps
    }
) => {

    const onChangeCallback = (e: ChangeEvent<HTMLInputElement>) => {
        onChange //    onChange
        && onChange(e) //     ( onChange  )

        onChangeText && onChangeText(e.currentTarget.value)
    }
    const onKeyPressCallback = (e: KeyboardEvent<HTMLInputElement>) => {
        onKeyPress && onKeyPress(e);

        onEnter //    onEnter
        && e.key === 'Enter' //     Enter
        && onEnter() //   
    }

    const finalSpanClassName = `${s.error} ${spanClassName ? spanClassName : ''}`
    const finalInputClassName = `${error ? s.errorInput : s.superInput} ${className}` // need to fix with (?:) and s.superInput

    return (
        <div className={s.input_wrapper}>
            <div className={s.input_inner_wrapper}>
                <input
                    type={'text'}
                    onChange={onChangeCallback}
                    onKeyPress={onKeyPressCallback}
                    className={finalInputClassName}

                    {...restProps} //        (value   )
                />
                <span className={s.focus_border}>

                </span>
            </div>
            <div className={s.error_wrapper}>
                {error && <span className={finalSpanClassName}>{error}</span>}
            </div>
        </div>
    )
}

export default SuperInputText
